/* ==========================================================================
   WooCommerce Widgets styles - begin
   ========================================================================== */

.widget.woocommerce {
    $thumb: 77px;
    $inner-padding: 15px; // distance between image and content
    $widget-list-item-padding: 15px; // same as sidebar vars

    clear: both;

    li {
        position: relative;
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // title

    .product-title {
        @include qodefH5();
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // star rating

    li:not(.wc-layered-nav-rating) {
        .star-rating {
            margin-bottom: 7px;
            margin-top: 7px;

            span {
                max-width: 100px;
            }
        }
    }

    li.wc-layered-nav-rating {
        a {
            text-decoration: none;
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // price

    .amount {
        color: inherit;

        span { // currency symbol
            color: inherit;
        }
    }

    del,
    ins {
        display: block;

        // currency symbol
        span {
            color: inherit;
        }
    }

    ins {
        text-decoration: none;
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // layered nav

    &.widget_layered_nav {
        .chosen {
            a {
                color: $first-main-color;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // price filter

    &.widget_price_filter {
        form {
            margin-top: 32px;
        }

        .price_slider_wrapper {
            .ui-widget-content {
                background-color: #e1e1e1;
                height: 2px;
                position: relative;

                .ui-slider-handle {
                    background-color: $default-heading-color;
                    border-radius: 50%;
                    cursor: pointer;
                    height: 9px;
                    margin: 0 0 0 -1px;
                    outline: medium none;
                    position: absolute;
                    top: -4px;
                    width: 9px;
                    z-index: 2;

                    &:last-child {
                        transform: translateX(-50%);
                    }
                }

                .ui-slider-range {
                    background-color: $default-heading-color;
                    display: block;
                    height: 2px;
                    position: absolute;
                    top: 0;
                    z-index: 1;
                }
            }
        }

        .price_slider_amount {
            margin: 24px 0 0;

            .button {
                float: right;
                background-color: transparent;
                color: $default-heading-color;
                padding: 0;
                width: auto;

                &:before {
                    content: '';
                    position: absolute;
                    bottom: 4px;
                    left: 0;
                    height: 1px;
                    background-color: currentColor;
                    width: 100%;
                    -webkit-transform-origin: 100% 50%;
                    -moz-transform-origin: 100% 50%;
                    transform-origin: 100% 50%;
                    transition: transform .4s cubic-bezier(.28,.38,0,.81);
                }

                &:after {
                    content: '';
                    position: absolute;
                    left: 0;
                    bottom: 4px;
                    height: 1px;
                    background-color: currentColor;
                    width: 100%;
                    transform: scaleX(0);
                    -webkit-transform-origin: 0 50%;
                    -moz-transform-origin: 0 50%;
                    transform-origin: 0 50%;
                    transition: transform .4s cubic-bezier(.28,.38,0,.81) .2s;
                }

                &:hover {
                    background-color: transparent;
                    color: $default-heading-color;
                    border-color: transparent;
                    padding: 0;

                    &:before {
                        transform: scaleX(0);
                    }
                    &:after {
                        transform: scaleX(1);
                    }
                }
            }

            .price_label {
                float: left;
                line-height: 24px; // to match button height
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with images

    &.widget_shopping_cart,
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products {
        ul {
            li {
                min-height: $thumb;
                position: relative;

                a {
                    img {
                        left: 0;
                        max-width: $thumb;
                        position: absolute;
                        top: 0;
                        width: 100%;
                    }

                    span {
                        box-sizing: border-box;
                        display: inline-block;
                        padding: 17px 0 0 0;
                        position: relative;
                        font-weight: 600;
                        text-transform: capitalize;
                        font-family: $default-heading-font;
                        font-size: 15px;
                        letter-spacing: 0;
                        @include qodefUnderline;
                        line-height: 1em;
                        margin: 0 0 2px #{$thumb + $inner-padding};

                        &:hover {
                            color: $default-heading-color;
                        }
                    }
                }

                > *:not(a) {
                    clear: both;
                    float: left;
                    margin: 0 0 0 #{$thumb + $inner-padding};
                }
            }
        }
    }

    &.widget_products {

        .qodef-widget-title-holder {
            margin-bottom: 28px;
        }

        ul li {
            ins {
                clear: none;
                margin-left: 5px;
            }
            
            .star-rating {
                display: none;
            }

            a span {
                float: left;
            }
        }
    }

    &.widget_product_categories {
        ul {
            li {
                padding: 5px 0;

                a {
                    font-weight: 700;
                    @include qodefUnderline;
                    color: $default-text-color;

                    &:hover{
                        color: $default-text-color;
                    }
                }
            }
        }

        >ul {
            >li {
                >a {
                    color: $default-heading-color;

                    &:hover {
                        color: $default-heading-color;
                    }
                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with images and plain text next to image

    &.widget_shopping_cart {
        a {
            display: block;
            padding: 0 0 0 #{$thumb + $inner-padding};

            @include qodefH5();
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // recent reviews

    //&.widget_recent_reviews {
    //    .reviewer {
    //    }
    //}

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // shopping cart

    &.widget_shopping_cart {
        .remove {
            font-size: 0;
            font-weight: normal;
            line-height: 1;
            position: absolute;
            right: 0;
            top: 5px;
            text-decoration: none;

            &:after {
                content: "\4d";
                display: block;
                font-family: 'ElegantIcons';
                font-size: 15px;
                line-height: 15px;
                position: relative;
            }
        }

        .variation {
            @include qodefRelativeHolderLayout();

            > * {
                display: inline-block;
                margin: 0;
                vertical-align: middle;

                p {
                    margin: 0;
                }
            }
        }

        .total {
            margin: 17px 0 13px;
        }

        .buttons {
            .button {
                @include qodefButtonDefaultStyle();
                @include qodefButtonSize();
                @include qodefButtonSolidColor();

                &:hover {
                    @include qodefButtonSolidHoverColor();
                }

                &:first-child {
                    float: left;
                }

                &:last-child {
                    float: right;
                }
            }
        }
    }
}

/* ==========================================================================
   WooCommerce Widgets styles - end
   ========================================================================== */