/* ==========================================================================
   WooCommerce Result and Ordering styles - begin
   ========================================================================== */
.qodef-woocommerce-page {

    .woocommerce-result-count,
    .woocommerce-ordering {
        display: inline;
        width: 50%;
        margin: 0 0 30px;
        float: left;
    }

    .woocommerce-result-count {
        line-height: $sselect-input-height;
        font-size: 16px;
    }

    .woocommerce-ordering {
        text-align: right;

        .orderby {
            width: auto;
            min-width: 200px;
            margin: 0;
            text-align: initial;
        }

        .select2 {
            margin-bottom: 0;

            .select2-selection--single {
                border: none;
                text-align: right;
                padding-right: 25px;
                font-style: normal;
                
                @include phone-landscape {
                    text-align: left;
                }
            }

            .select2-selection__arrow {
                border: none;
                width: auto;
                top: -2px;

                @include laptop-landscape-mac {
                    top: 0;
                }
            }
        }
    }
}

/* ==========================================================================
   WooCommerce Result and Ordering styles - end
   ========================================================================== */