/* ==========================================================================
   WooCommerce Quantity Inputs styles - begin
   ========================================================================== */

.woocommerce-page .qodef-content,
div.woocommerce {

    .qodef-quantity-buttons {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        border: 1px solid $default-border-color;
        height: 55px;

        .qodef-quantity-input,
        .qodef-quantity-minus,
        .qodef-quantity-plus {
            display: inline-block;
            vertical-align: middle;
            background-color: transparent;
            text-align: center;
            -webkit-user-select: none;
            box-sizing: border-box;
            color: $default-heading-color;
        }

        .qodef-quantity-input {
            position: relative;
            padding: 0 17px;
            margin: 0 ;
            font-family: inherit;
            line-height: 61px;
            outline: 0;
            border: none;
            font-weight: 600;

            @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
                padding: 21px 17px;
                line-height: 0;
            }
        }

        .qodef-quantity-minus,
        .qodef-quantity-plus {
            position: absolute;
            width: auto;
            cursor: pointer;
            transition: color .15s ease-in-out;
            top: 50%;
            transform: translateY(-50%);
            font-size: 20px;
            z-index: 50;

            &:hover {
                color: $first-main-color;
            }
        }

        .qodef-quantity-plus {
            right: 13px;
        }

        .qodef-quantity-minus {
            left: 13px;
        }
    }
}

.woocommerce-page .qodef-content,
div.woocommerce {

    input[type="text"],
    input[type="email"],
    input[type="tel"],
    input[type="password"],
    textarea {
        @extend %input-style;
    }

    .coupon {

        input[type="text"] {
            width: 200px;
            height: 52px;
        }
    }
}
/* ==========================================================================
   WooCommerce Quantity Inputs styles - end
   ========================================================================== */