/* ==========================================================================
   WooCommerce Single Product Page styles - begin
   ========================================================================== */

.qodef-woo-single-page {
	
	&.qodef-woo-single-thumb-on-left-side {
		
		.qodef-single-product-content {
			
			.images {
				
				@media only screen and (min-width: $ipad-portrait-plus-pixel) {
					
					figure {
						width: 100%;
						margin: 0;
						overflow: hidden;
					}
					
					$thumb_size: 140;
					$space_between_items: 9;
					
					.woocommerce-product-gallery__image {
						
						&:first-child {
							$first_thumb_offset: $thumb_size + $space_between_items;
							
							left: #{$first_thumb_offset}px;
							width: calc(100% - #{$first_thumb_offset}px);
						}
					}
					
					&.woocommerce-product-gallery--with-images {
						
						.woocommerce-product-gallery__image {
							
							&:not(:first-child) {
								position: absolute;
								top: 0;
								left: 0;
								width: #{$thumb_size}px;
								padding: 0 0 #{$space_between_items}px;
								box-sizing: border-box;
								
								a {
									height: 100%;
									
									img {
										height: 100%;
									}
								}
							}
						}
					}
					
					$thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
					@for $i from 0 to length($thumb_3_columns_label) {
						&.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
							$column_value: $i+2;
							$column_size: 100 / $column_value;
							$column_width: calc(#{100% / $column_value} + #{$space_between_items / $column_value}px);
							
							.woocommerce-product-gallery__image {
								
								&:not(:first-child) {
									height: $column_width;
								}
								
								$thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
								@for $i from 0 to length($thumbnail_numbers) {
									$thumbnail_number_value: nth($thumbnail_numbers, $i+1);
									
									&:nth-child(#{$thumbnail_number_value}#{n}) {
										$first_param: ($column_size * $i)#{'%'};
										$second_param: ($space_between_items / $column_value * $i)#{px};
										$topPos: calc(#{$first_param} + #{$second_param});
										
										top: $topPos;
									}
								}
							}
						}
					}
				}
			}
		}
	}
    
    .product {
	    @include qodefRelativeHolderLayout();
	    float: none;
	    margin: 0 0 50px;
    }
        
    .qodef-single-product-content {
        @include qodefRelativeHolderLayout();
        clear: both;
        margin: 0 0 20px;
	
	    .images {
		    position: relative;
		    display: inline-block;
		    vertical-align: middle;
		    width: 51%;
		    margin: 0;
		    padding: 0 24px 0 0;
		    float: left;
		    box-sizing: border-box;
		    
		    figure {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0 -10px;
			
			    a {
				    @include qodefRelativeHolderLayout();
				
				    img {
					    position: relative;
					    width: 100%;
					    display: block;
				    }
			    }
		    }
		
		    .woocommerce-product-gallery__trigger {
				position: absolute;
			    top: 10px;
			    right: 39px;
			    width: 30px;
			    height: 30px;
			    line-height: 30px;
			    text-align: center;
			    z-index: 10;
		    }
		    
		    .woocommerce-product-gallery__image {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0;
			    padding: 0 6px;
			    box-sizing: border-box;
			    
			    &:first-child {
				    left: 6px;
				    width: calc(100% - 12px);
				    padding: 0;
			    }
			
			    .zoomImg {
				    cursor: crosshair;
			    }
		    }
		    
		    &.woocommerce-product-gallery--with-images {
			
			    .woocommerce-product-gallery__image {
				
				    &:not(:first-child) {
					    float: left;
					    padding-top: 9px;
				    }
			    }
		    }
		
		    $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
		    @for $i from 0 to length($thumb_3_columns_label) {
			    &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
				    $column_width: 100%/($i+2);
				
				    .woocommerce-product-gallery__image {
					
					    &:not(:first-child) {
						    width: $column_width;
					    }
				    }
			    }
		    }
	    }
    }
	
    .qodef-single-product-summary {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 49%;
        padding: 0 0 0 67px;
        float: left;
        box-sizing: border-box;
	
	    .qodef-single-product-title {
		    display: block;
		    margin: 19px 0 10px;
	    }
	
	    .price {
		    @include qodefWooCommercePriceStyle();
			margin-top: 27px;
			font-weight: 600;

			* {
				color: $default-heading-color;
				font-size: 22px;
			}
	    }
	
	    .woocommerce-product-rating {
		    @include qodefRelativeHolderLayout();
		    line-height: 1;
		    margin: 0 0 3px;
	    }
	
	    .woocommerce-product-details__short-description {
		    @include qodefRelativeHolderLayout();
		    margin: 24px 0 0;
		
		    p {
			    margin: 0;
		    }
	    }
	
	    .product_meta {
		    margin: 32px 0 0;
		
		    > span {
			    display: block;
			    margin: 13px 0;
				font-weight: 600;
				color: $default-heading-color;

				>span,
				> a {
					font-weight: 400;
					color: $default-text-color;

					&:first-of-type {
						padding-left: 10px;
					}
				}
			
			    a {
					position: relative;
				    color: $default-text-color;

					&:before {
						content: '';
						position: absolute;
						bottom: 0;
						left: 0;
						height: 1px;
						background-color: currentColor;
						width: 100%;
						transform: scaleX(0);
						-webkit-transform-origin: 100% 50%;
						-moz-transform-origin: 100% 50%;
						transform-origin: 100% 50%;
						transition: transform .4s cubic-bezier(.28,.38,0,.81);
					}
				
				    &:hover {
					    color: $default-text-color;
						&:before {
							transform: scaleX(1);
							-webkit-transform-origin: 0% 50%;
							-moz-transform-origin: 0% 50%;
							transform-origin: 0% 50%;
						}
				    }
			    }
		    }
	    }
	
	    .qodef-woo-social-share-holder {
		    @include qodefRelativeHolderLayout();
		    margin: 0;
		
		    > * {
			    display: table-cell;
			    vertical-align: top;
		    }
		
		    > span {
			    padding: 0 9px 0 0;
		    }
	    }
	
	    p.stock.out-of-stock,
	    p.stock.in-stock {
		    color: $default-heading-color;
		    font-weight: bold;
		    margin: 18px 0 0;
	    }
	
	    form.cart {
		    @include qodefRelativeHolderLayout();
		    margin: 36px 0 0;

			button {

			}
	    }
	
	    .qodef-quantity-buttons {
		    margin: 0 12px 0 0;
	    }
    }
	
	.woocommerce-tabs {
		@include qodefRelativeHolderLayout();
		margin: 39px 0 0;
		border: 1px solid $default-border-color;
		
		ul.tabs {
			@include qodefRelativeHolderLayout();
			list-style: none;
			margin: 0;
			padding: 0;
			clear: both;

			&:after {
				content: '';
				position: absolute;
				height: 1px;
				width: 100%;
				background-color: $default-border-color;
				left: 0;
				bottom: 0;
			}
			
			> li {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				z-index: 50;

				&.active {
					border-right: 1px solid $default-border-color;
					border-left: 1px solid $default-border-color;
					border-bottom: 1px solid #fff;
					margin-left: -1px;
				}
				
				a {
					position: relative;
					display: inline-block;
					vertical-align: top;
					padding: 24px 34px 22px;
					font-size: 16px;
					font-weight: 600;
				}

				&:hover {
					a {
						color: $default-heading-color;
					}
				}
			}
		}
		
		.entry-content {
			@include qodefRelativeHolderLayout();
			box-sizing: border-box;
			padding: 25px 32px 33px;
			
			> h2 {
				display: none;
			}
			
			> p {
				margin: 34px 32px;
			}
		}
		
		table {
			tr, th {
				border: 0;
			}
			
			td, th {
				padding: 3px 0;
				vertical-align: middle;
				text-align: left;
				
				p {
					margin: 0;
				}
			}
			
			th {
				color: $default-heading-color;
				width: 1%;
				padding: 0 10px 0 0;
				font-weight: 600;
			}
			
			td {
				width: 99%;
			}
		}
		
		#reviews {
			@include qodefRelativeHolderLayout();
			
			h2 {
				font-size: 18px;
				line-height: 24px;
				margin: 8px 0 24px;
				font-weight: 600;
			}
			
			ol.commentlist {
				list-style: none;
				
				li {
					margin: 0 0 15px;
					
					&:last-child {
						margin: 0;
					}
				}
				
				.comment_container {
					@include qodefRelativeHolderLayout();
					
					img {
						float: left;
						width: 70px;
					}
				}
				
				.comment-text {
					padding: 0 0 0 90px;
					
					p {
						margin: 0;

						&.meta {
							font-size: 16px;
							font-weight: 600;
							color: $default-heading-color;
						}
					}
				}
			}
			
			.comment-respond {
				margin-top: 15px;
				
				.comment-reply-title {
					margin: 0 0 8px;
					font-size: 16px;
					font-weight: 600;
				}

				.comment-form-rating label {
					font-size: 16px;
					font-weight: 600;
					padding-top: 17px;
					display: block;
				}
				
				p {
					margin: 0;

					> label {
						padding-top: 16px;
						display: block;
					}

					#comment {
						height: 42px;
					}
				}
				
				.stars {
					overflow: hidden;
					line-height: .7em;
					
					a {
						position: relative;
						display: inline-block;
						color: transparent;
						font-size: 0;
						margin: 0;
						@include qodefWooCommerceSingleProductRatingsStyle();

						&:before {
							line-height: 2.2em;
						}
					}
				}
			}
		}
	}
	
	.related.products,
	.upsells.products {
		margin: 48px 0 0;
		
		> h2 {
			display: block;
			margin: 36px 0 39px;
			@include qodefH3();
		}
		
		.product {
			
			.qodef-pl-text-wrapper {
				margin-bottom: 0;
			}
		}
	}
}
/* ==========================================================================
   WooCommerce Single Product Page styles - end
   ========================================================================== */