/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.qodef-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    height: 100%;
    margin: 0 0 0 10px;

    .touch & {
        cursor: pointer;
    }

    .qodef-shopping-cart-inner {
        position: relative;
        display: table;
        height: 100%;
    }

    .qodef-header-cart {
        position: relative;
        display: table-cell;
        vertical-align: middle;
        line-height: 1;

        .qodef-cart-icon {
            position: relative;
            display: inline-block;
            vertical-align: top;
        }

        &.qodef-header-cart-icon-pack {
            
            .qodef-cart-icon {
                font-size: 20px;
                line-height: 1em;
    
                * {
                    display: block;
                    line-height: inherit;
        
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }

        &.qodef-header-cart-svg-path {
            
            .qodef-cart-icon {

                > * {
                    vertical-align: middle;
                }
                
                > *, 
                path {
                    fill: currentColor;
                    stroke: currentColor;
                }
            }
        }

        &.qodef-header-cart-predefined {
            &:before {
                content: '';
                position: absolute;
                top: calc(50% + 6px);
                left: 0;
                height: 1px;
                background-color: currentColor;
                width: 100%;
                -webkit-transform-origin: 100% 50%;
                -moz-transform-origin: 100% 50%;
                transform-origin: 100% 50%;
                transition: transform .4s cubic-bezier(.28,.38,0,.81);
            }

            &:after {
                content: '';
                position: absolute;
                left: 0;
                top: calc(50% + 6px);
                height: 1px;
                background-color: currentColor;
                width: 100%;
                transform: scaleX(0);
                -webkit-transform-origin: 0 50%;
                -moz-transform-origin: 0 50%;
                transform-origin: 0 50%;
                transition: transform .4s cubic-bezier(.28,.38,0,.81) .2s;
            }

            &:hover {
                &:before {
                    transform: scaleX(0);
                }
                &:after {
                    transform: scaleX(1);
                }
            }
        }

        &.qodef-header-cart {
            font-weight: 400;
            font-size: 14px;
            color: #c0c0c0;
        }
    }

    &:hover {

        .qodef-shopping-cart-dropdown {
            top: 100%;
            opacity: 1;
            visibility: visible;
        }
    }
}

.qodef-dark-header .qodef-page-header > div:not(.qodef-sticky-header):not(.fixed) .qodef-shopping-cart-holder .qodef-header-cart {
    color: $header-dark-color;

    &:hover {
        color: $header-dark-hover-color;
    }
}

.qodef-light-header .qodef-page-header > div:not(.qodef-sticky-header):not(.fixed) .qodef-shopping-cart-holder .qodef-header-cart {
    color: $header-light-color;

    &:hover {
        color: $header-light-hover-color;
    }
}

.qodef-shopping-cart-dropdown {
    position: absolute;
    top: calc(100% + 50px);
    left: auto;
    right: -10px;
    width: 265px;
    background-color: #171717;
    text-align: left;
    z-index: 1000;
    opacity: 0;
    visibility: hidden;
    box-sizing: border-box;
    transition: all 0.35s cubic-bezier(.53,.01,.18,1);

    ul {
        margin: 42px 25px;
        padding: 0;

        li {
            @include qodefTableLayout();
            padding: 3px 0;
            box-sizing: border-box;

            &.qodef-empty-cart {
                color: #b4b3b3;
                border-bottom: 0;
            }

            &:first-child {
                padding-top: 0;
            }

            &:last-child {
                padding-bottom: 0;
            }
        }
    }

    .qodef-item-image-holder {
        position: relative;
        display: table-cell;
        height: 100%;
        width: 74px;

        img {
            display: block;
        }
    }

    .qodef-item-info-holder {
        @include qodefTableCellLayout();
        padding-left: 20px;

        .qodef-product-title {
            display: block;
            margin: 0;
            padding: 0 20px 0 0;
            box-sizing: border-box;
            color: #fff;
            font-weight: 400;


            a {
                &:hover {
                    color: $default-text-color;
                }
            }
        }

        .remove {
            position: absolute;
            top: -1px;
            right: 0;
	        font-size: 22px;
	        line-height: 1;
            color: #fff;

            &:hover {
                color: $default-text-color;
            }

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .qodef-quantity,
        .amount{
            display: inline-block;
            margin: 3px 0 0;
            color: #b4b3b3;
        }
    }

    .qodef-cart-bottom {
        padding: 15px 0 0;

        .qodef-subtotal-holder {
            position: relative;
            display: table;
            width: 100%;
            margin: 9px 0 23px;
            font-weight: 400;

            > * {
                display: table-cell;
                vertical-align: top;
                line-height: 20px;
            }

            .qodef-total {
                color: #fff;
            }

            .qodef-total-amount {
                text-align: right;
                color: #b4b3b3;
            }
        }

        .qodef-view-cart,
        .qodef-checkout {
            @include qodefRelativeHolderLayout();
            height: 53px;
            line-height: 53px;
            padding: 0 26px;
            font-weight: 600;
            color: $default-heading-color;
            background-color: #f8f8f8;
            text-align: center;
            box-sizing: border-box;
            transition: color .2s ease-out, background-color .2s ease-out;
            width: 100%;
            text-transform: lowercase;

            span {
                position: relative;

                &:before {
                    content: '';
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    height: 1px;
                    background-color: currentColor;
                    width: 100%;
                    -webkit-transform-origin: 100% 50%;
                    -moz-transform-origin: 100% 50%;
                    transform-origin: 100% 50%;
                    transition: transform .4s cubic-bezier(.28,.38,0,.81);
                }

                &:after {
                    content: '';
                    position: absolute;
                    left: 0;
                    bottom: 0;
                    height: 1px;
                    background-color: currentColor;
                    width: 100%;
                    transform: scaleX(0);
                    -webkit-transform-origin: 0 50%;
                    -moz-transform-origin: 0 50%;
                    transform-origin: 0 50%;
                    transition: transform .4s cubic-bezier(.28,.38,0,.81) .2s;
                }
            }

            &:hover {
                span {
                    &:before {
                        transform: scaleX(0);
                    }
                    &:after {
                        transform: scaleX(1);
                    }
                }
            }
        }

        .qodef-view-cart {
            margin: 3px 0 5px;
        }
    }
}

.qodef-vertical-menu-area {

    .qodef-shopping-cart-dropdown {
        display: none !important;
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */