/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - begin
   ========================================================================== */

@mixin qodefWooCommerceMarksGeneralStyle() {
    position: absolute;
    display: block;
    top: 0;
    width: auto;
    height: 39px;
    margin: 0;
    padding: 0 30px;
    color: $default-heading-color;
    background-color: #fff;
    font-size: 15px;
    line-height: 39px;
    text-align: center;
    z-index: 100;
    box-sizing: border-box;
    transform: translateZ(0);
    -moz-transform: none;
    font-weight: 600;
}

@mixin qodefWooCommerceSaleStyle() {
    right: 0;
}

@mixin qodefWooCommerceOutOfStockStyle() {
    right: 0;
}

@mixin qodefWooCommerceNewProductStyle() {
    right: 0;
}

/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Price mixin styles - begin
   ========================================================================== */

@mixin qodefWooCommercePriceStyle() {
    position: relative;
    display: block;
    margin: 8px 0 0;
    line-height: 1;
    color: $default-heading-color;
    
    * {
        font-size: inherit;
        line-height: inherit;
    }
    
    del {
        margin: 0 8px 0 0;
        color: rgba($default-heading-color, .8);
        text-decoration: none;
        position: relative;

        &:after {
            content: '';
            position: absolute;
            height: 1px;
            width: 100%;
            left: 0;
            top: 40%;
            background-color: currentColor;

            @include laptop-landscape-large {
                top: 50%
            }

            @include ipad-landscape {
                top: 9px;
            }
        }
    }
    
    ins {
        text-decoration: none;
    }
}
/* ==========================================================================
   Price mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Star Ratings mixin styles - begin
   ========================================================================== */

%star-ratings {
    font-family: "ElegantIcons";
    font-size: 12px;
    line-height: 1;
    letter-spacing: 7px;
    color: $default-text-color;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

@mixin qodefWooCommerceRatingsStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    @extend %star-ratings;
    overflow: hidden;
    
    &:before {
        content: "\e031\e031\e031\e031\e031";
    }
    
    span {
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;
        color: $default-heading-color;
        
        &:before {
            content: "\e033\e033\e033\e033\e033";
        }
    }
}

@mixin qodefWooCommerceSingleProductRatingsStyle() {
    
    &:before {
        content: "\e031";
        position: relative;
        display: inline-block;
        vertical-align: middle;
        @extend %star-ratings;
        overflow: hidden;
        z-index: -1;
    }
    
    &.active:after {
        content: "\e033\e033\e033\e033\e033";
        position: absolute;
        right: 0;
        bottom: 7px;
        display: block;
        @extend %star-ratings;

        @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
            width: 95px;
        }

        @supports (-ms-ime-align: auto) {
            width: 95px;
        }
    }
}
/* ==========================================================================
   Star Ratings mixin styles - end
   ========================================================================== */