/* ==========================================================================
   Product List shortcode styles - begin
   ========================================================================== */

.qodef-pl-holder {
    @include qodefRelativeHolderLayout();

	.qodef-pl-outer.qodef-ajax-loading {
		opacity: 0;
	}
	
	&.qodef-info-on-image {
		
		.qodef-pli {
			&:hover {
				.qodef-pli-text {
                    opacity: 1;
                    transform: translateX(0);
                    transition: transform .5s cubic-bezier(0.46, 0.01, 0.41, 1), opacity .0s;
				}
                .qodef-pli-price {
                    opacity: 1;
                    transform: translateX(0);
                }
			}

			@include phone-landscape {
				&.qodef-masonry-size-large-width {
					.qodef-pli-text {
						padding: 20px 40px;
					}
				}
			}

			.qodef-pli-add-to-cart {
				margin: 25px 0 0;
			}

			.qodef-pli-text {
				@include qodefAbsoluteHolderLayout();
				opacity: 0;
				padding: 41px;
				box-sizing: border-box;
				z-index: 20;
                transform: translateX(-100%);
                transition: transform .5s cubic-bezier(0.46, 0.01, 0.41, 1), opacity .0s .5s;

				.qodef-pli-text-outer {
					background-color: #fff;

					.qodef-pli-title {
						color: $default-heading-color;
					}

					.qodef-pli-price {
						color: $default-text-color;
						left: 0;
						margin-top: 7px;
					}
				}

				.qodef-pli-add-to-cart.qodef-default-skin {
					margin-top: 7px;

					a.button,
					a.added_to_cart{
						background-color: transparent;
						padding: 0;
						color: $default-heading-color;

						&:before {
							content: '';
							position: absolute;
							bottom: 4px;
							left: 0;
							height: 1px;
							background-color: currentColor;
							width: 100%;
							-webkit-transform-origin: 100% 50%;
							-moz-transform-origin: 100% 50%;
							transform-origin: 100% 50%;
							transition: transform .4s cubic-bezier(.28,.38,0,.81);
						}

						&:after {
							content: '';
							position: absolute;
							left: 0;
							bottom: 4px;
							height: 1px;
							background-color: currentColor;
							width: 100%;
							transform: scaleX(0);
							-webkit-transform-origin: 0 50%;
							-moz-transform-origin: 0 50%;
							transform-origin: 0 50%;
							transition: transform .4s cubic-bezier(.28,.38,0,.81) .2s;
						}

						&:hover {
							&:before {
								transform: scaleX(0);
							}
							&:after {
								transform: scaleX(1);
							}
						}
					}
				}
			}
		}
	}

    &.qodef-info-below-image {

        .qodef-pli {
            .qodef-pli-inner .qodef-pli-image {
                overflow: hidden;

                img {
                    display: block;
                    transform: scale(1);
                    transition: transform 1s cubic-bezier(.165,.84,.44,1);
                }
            }

			.qodef-pli-price {
				opacity: 1;
				transition: all .45s ease;
				transform: translateX(0);
			}

			.qodef-pli-text-wrapper .qodef-pli-text {
				opacity: 0;
				transform: translateX(-100%);
				transition: all 0s ease;
			}

            &:hover {
                .qodef-pli-inner .qodef-pli-image {
                    img {
                        transform: scale(1.1);
                        transition: transform 2s cubic-bezier(.165,.84,.44,1);
                    }
                }
				.qodef-pli-price {
					opacity: 0;
					transform: translateX(15%);
					transition: all 0s ease;
				}
				.qodef-pli-text-wrapper .qodef-pli-text {
					opacity: 1;
					transform: translateX(-50%);
					transition: all .45s ease;
				}
            }
        }
    }

	.qodef-prl-loading {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: 101;
		background-color: transparent;
		display: none;
		margin-top: 45px;
		box-sizing: border-box;
		text-align: center;
		padding: 120px;

		.qodef-prl-loading-msg {
			font-size: 16px;
			line-height: 1.3em;
			color: $default-heading-color;
			font-weight: 600;
		}
	}

	.qodef-pl-categories {
		margin-bottom: 42px;

		.qodef-pl-categories-label {
			display: none;
		}

		ul {
			li {
				display: inline-block;
				list-style: none;
				font-weight: 600;

				&:last-child {
					a {
						margin: 0;

						&:after {
							display: none;
						}
					}
				}

				a {
					margin-right: 8px;
					position: relative;

					&:before {
						content: '';
						position: absolute;
						left: 0;
						bottom: -3px;
						height: 2px;
						width: 25px;
						background-color: $default-heading-color;
						transition: opacity .2s ease;
						opacity: 0;
					}

					&.active,
					&:hover {
						color: $default-heading-color;

						&:before {
							opacity: 1;
							transition: opacity .2s ease;
						}
					}

					&:after {
						content: '/';
						position: relative;
						right: -5px;
					}
				}
			}
		}
	}

    .qodef-pli {
        padding: 0;
        z-index: 100;

	    .qodef-pli-title {
		    margin: 0 0 4px;
			line-height: 1.3em;
	    }
	
	    .qodef-pli-category {
		    margin: 11px 0 3px;
	    }

        .qodef-pli-excerpt {
            color: $default-heading-color;
	        line-height: 1.3em;
            margin: 11px 0 3px;
        }

	    .qodef-pli-rating-holder {
		    @include qodefRelativeHolderLayout();
		    line-height: 1;
		    margin: 10px 0 3px;
	    }

	    .qodef-pli-rating {
		    @include qodefWooCommerceRatingsStyle();
	    }

        .qodef-pli-price {
	        @include qodefWooCommercePriceStyle();
			color: $default-text-color;
			left: 0;
			transition: all .45s ease;
        }

		&:hover {
			.qodef-pli-price {
				opacity: 0;
				transform: translateX(25%);
			}

			.qodef-pli-text {
                opacity: 1;
                transform: translateX(-50%);
			}

			.qodef-pli-onsale,
			.qodef-pli-out-of-stock,
			.qodef-pli-new-product {
				opacity: 0;
				transition: all .2s ease;
			}
		}
    }

    .qodef-pli-inner {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        overflow: hidden;

        .touch & {
            cursor: pointer;
        }

        .qodef-pli-image {
            @include qodefRelativeHolderLayout();

            img {
                display: block;
                width: 100%;
            }

            .qodef-pli-onsale,
            .qodef-pli-out-of-stock,
            .qodef-pli-new-product {
	            @include qodefWooCommerceMarksGeneralStyle();
            }

            .qodef-pli-onsale {
	            @include qodefWooCommerceSaleStyle();
            }

            .qodef-pli-out-of-stock {
	            @include qodefWooCommerceOutOfStockStyle();
            }
	
	        .qodef-pli-new-product {
		        @include qodefWooCommerceNewProductStyle();
	        }
        }

        .qodef-pli-text-outer {
	        @include qodefTableLayout();
	        text-align: center;
        }

        .qodef-pli-text-inner {
            @include qodefTableCellLayout();

            .qodef-pli-title,
            .qodef-pli-category a,
            .qodef-pli-excerpt,
            .qodef-pli-rating,
            .qodef-pli-price {
                color: #fff;
            }
	
	        .qodef-pli-category a:hover {
		        color: rgba(#fff, .8);
	        }

	        .qodef-pli-add-to-cart {

		        &.qodef-default-skin {

			        .button,
			        .added_to_cart {
				        @include qodefButtonSolidColor();

				        &:hover {
				            @include qodefButtonSolidHoverColor();
				        }
			        }
		        }

		        &.qodef-light-skin {

			        .button,
			        .added_to_cart {
				        color: $default-heading-color;
				        background-color: #fff;

				        &:hover {
					        color: #fff;
					        background-color: $first-main-color;
				        }
			        }
		        }
		
		        &.qodef-dark-skin {
			
			        .button,
			        .added_to_cart {
				        color: #fff;
				        background-color: $default-heading-color;
				
				        &:hover {
					        color: #fff;
					        background-color: $first-main-color;
				        }
			        }
		        }
	        }

            .button,
            .added_to_cart {
	            @include qodefButtonDefaultStyle();
	            @include qodefButtonSize();
	            white-space: nowrap;
	            z-index: 3;
            }

            .button.added {
                display: none;
            }
        }

	    .qodef-pli-link {
		    @include qodefAbsoluteHolderLayout();
	    }
    }

    .qodef-pli-text-wrapper {
        @include qodefRelativeHolderLayout();
		margin: 22px 0 22px;
		text-align: center;
		overflow: hidden;

		.qodef-pli-text {
			box-sizing: border-box;
			position: absolute;
			left: 50%;
			bottom: -3px;
            transform: translateX(-100%);
            opacity: 0;
            transition: all .45s ease;

			.button {
				color: $default-text-color;
				font-weight: 400;
				position: relative;
				white-space: nowrap;
				left: -5px;
				
				&.added {
					display: none;
				}

				&.added_to_cart {
					&:hover {
						color: $default-heading-color;
					}
				}

				&:hover {
					color: $default-heading-color;
				}

				&:before {
					content: "\24";
					font-family: ElegantIcons;
					font-size: 17px;
					position: relative;
					right: 3px;
					top: 3px;
				}
			}
		}
    }
	
	&.qodef-product-info-light {
		
		.qodef-pli-inner {
			
			.qodef-pli-text-inner {
				
				.qodef-pli-title,
				.qodef-pli-category a,
				.qodef-pli-excerpt,
				.qodef-pli-rating,
				.qodef-pli-price {
					color: #fff;
				}
				
				.qodef-pli-category a:hover {
					color: rgba(#fff, .8);
				}
			}
		}
	}
	
	&.qodef-product-info-dark {
		
		.qodef-pli-inner {
			
			.qodef-pli-text-inner {
				
				.qodef-pli-title,
				.qodef-pli-category a,
				.qodef-pli-excerpt,
				.qodef-pli-rating,
				.qodef-pli-price {
					color: $default-heading-color;
				}
				
				.qodef-pli-category a:hover {
					color: rgba($default-heading-color, .8);
				}
			}
		}
	}
	
	/*************** Masonry Layout - begin ***************/
	
	&.qodef-masonry-layout {
		
		.qodef-pli-inner {
			width: 100%;
			height: 100%;
			
			.qodef-pli-image {
				height: 100%;
			}
		}
	}
	
	/*************** Masonry Layout - end ***************/
}
/* ==========================================================================
   Product List shortcode styles - end
   ========================================================================== */