/* ==========================================================================
   WooCommerce Product List page styles - begin
   ========================================================================== */

.qodef-pl-main-holder,
.related.products,
.upsells.products,
.cross-sells,
.qodef-content .woocommerce {
    @include qodefRelativeHolderLayout();
}

.qodef-pl-main-holder {
    margin: 0 0 22px;
    
    .qodef-woo-pl-info-on-image-hover & {
        margin: 0 0 50px;
    }
}

ul.products {
    list-style: none;
	margin: 0 -15px;
    clear: both;
    
    > .product {
        position: relative;
        display: inline-block;
	    vertical-align: middle;
	    float: left;
	    margin: 0 0 30px;
	    padding: 0 15px;
	    box-sizing: border-box;
        text-align: center;
        
        .touch & {
            cursor: pointer;
        }
        
        &:hover {
            
            .qodef-pl-text-wrapper{
                .qodef-pl-text {
                    opacity: 1;
                    transform: translateX(-50%);
                }

                .price {
                    opacity: 0;
                    transform: translateX(25%);
                }
            }
        }

        .qodef-woo-pl-info-below-image & {

            .qodef-pl-text-wrapper {
                .qodef-pl-text {
                    opacity: 0;
                    transform: translateX(-100%);
                    transition: all 0s ease;
                }
                .price {
                    opacity: 1;
                    transition: all .45s ease;
                    transform: translateX(0);
                }
            }

            &:hover {
                .qodef-pl-inner .qodef-pl-image {
                    img {
                        transform: scale(1.1);
                        transition: transform 2s cubic-bezier(.165,.84,.44,1);
                    }
                }
                .qodef-pl-text-wrapper {
                    .price {
                        opacity: 0;
                        transform: translateX(15%);
                        transition: all 0s ease;
                    }
                    .qodef-pl-text {
                        opacity: 1;
                        transform: translateX(-50%);
                        transition: all .45s ease;
                    }
                }
            }
        }
        
        .qodef-product-list-title {
            margin: 1px 0 4px;
        }
        
        .qodef-pl-rating-holder {
            @include qodefRelativeHolderLayout();
            line-height: 1;
            margin: 10px 0 3px;
        }
        
        .price {
            @include qodefWooCommercePriceStyle();
            transition: all .45s ease;
            * {
                color: $default-text-color;
            }
        }
        
        .button,
        .added_to_cart {
            margin: 25px 0 0;
    
            .qodef-woo-pl-info-below-image & {
                margin: 0;
                padding: 0;
                background-color: transparent;
                color: $default-text-color;
                font-weight: 400;
                position: relative;
                white-space: nowrap;
                left: -5px;

                &:before {
                    content: "\24";
                    font-family: ElegantIcons;
                    font-size: 17px;
                    position: relative;
                    right: 3px;
                    top: 3px;
                }

                &:hover {
                    color: $default-heading-color;
                    background-color: transparent;
                    border-color: transparent;
                }
            }
        }
        
        .button.added {
            display: none;
        }
        
        .qodef-pl-inner {
            @include qodefRelativeHolderLayout();
            overflow: hidden;
            
            .qodef-pl-image {
                @include qodefRelativeHolderLayout();
                overflow: hidden;
                
                img {
                    display: block;
                    width: 100%;
                    transform: scale(1);
                    transition: transform 1s cubic-bezier(.165,.84,.44,1);
                }
            }
            
            .qodef-pl-text-outer {
                @include qodefTableLayout();
                text-align: center;
            }
            
            .qodef-pl-text-inner {
                @include qodefTableCellLayout();
                
                .qodef-product-list-title,
                .price,
                .star-rating {
                    color: #fff;
                }
                
                .price del {
                    color: rgba(#fff, .8);
                }
            }
            
            > a {
                @include qodefAbsoluteHolderLayout();
            }
        }
        
        .qodef-pl-text-wrapper {
            @include qodefRelativeHolderLayout();
            margin: 22px 0 38px;
            text-align: center;
            overflow: hidden;

            .qodef-pl-text {
                position: absolute;
                left: 50%;
                bottom: -3px;
                transform: translateX(-100%);
                opacity: 0;
                transition: all .45s ease;
            }
        }
    }
}

$columns_space_label: ('no', 'tiny', 'small', 'normal', 'medium', 'large');
$columns_space_width: (0, 2, 10, 15, 20, 25);

@for $i from 0 to length($columns_space_label) {
    &.qodef-woo-#{nth($columns_space_label,$i+1)}-space {
        $column_width: nth($columns_space_width,$i+1);
        $column_width_margin: $column_width * 2;
        
        ul.products {
            @if($column_width == 0) {
                margin: 0;
            } @else {
                margin: 0 -#{$column_width}px;
            }
            
            > .product {
                padding: 0 #{$column_width}px;
                margin: 0 0 #{$column_width_margin}px;
            }
        }
    }
}

.qodef-pl-main-holder,
.related.products,
.upsells.products,
.cross-sells {
    
    $product_list_columns: ('columns-3', 'columns-4');
    
    @for $i from 0 to length($product_list_columns) {
        $column_width: 100%/($i+3);
        
        > .products {
            
            > .product {
                
                @media only screen and (min-width: $ipad-landscape-plus-pixel) {
                    
                    .qodef-woocommerce-#{nth($product_list_columns,$i+1)} & {
                        width: $column_width;
                        
                        &:nth-child(#{$i+3}n+1) {
                            clear: both;
                        }
                    }
                }
            }
        }
    }
}

.woocommerce {
    
    &.columns-1 {
        
        > .products {
            
            > .product {
                float: none;
                width: 100%;
            }
        }
    }
    
    $product_list_vc_columns: ('columns-2', 'columns-3', 'columns-4', 'columns-5', 'columns-6');
    
    @for $i from 0 to length($product_list_vc_columns) {
        &.#{nth($product_list_vc_columns,$i+1)} {
            $column_vc_width: 100%/($i+2);
            
            > .products {
                
                > .product {
                    width: $column_vc_width;
                    $product_list_vc_columns_label: #{nth($product_list_vc_columns,$i+1)};
                    
                    @if $product_list_vc_columns_label == 'columns-5' {
                        @media only screen and (min-width: $laptop-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-6' {
                        @media only screen and (min-width: $laptop-landscape-large-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-3' {
                        @media only screen and (min-width: $ipad-portrait-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-2' {
                        @media only screen and (min-width: $phone-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else {
                        @media only screen and (min-width: $ipad-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    }
                }
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Product List page styles - end
   ========================================================================== */