/* ==========================================================================
   WooCommerce Buttons styles - begin
   ========================================================================== */

.woocommerce-page .qodef-content,
div.woocommerce {
    
    a.button,
    a.added_to_cart,
    input[type="submit"],
    button[type="submit"]:not(.qodef-search-submit),
    .wc-forward:not(.added_to_cart):not(.checkout-button) {
        @include qodefButtonDefaultStyle();
        @include qodefButtonSize('medium');
        cursor: pointer;
        z-index: 3;
        background-color: #f8f8f8;
        color: $default-heading-color;
        border: 1px solid transparent;

        &:hover {
            background-color: $default-heading-color;
            color: #fff;
        }
    }
    
    a.button.added {
        display: none;
    }
}
/* ==========================================================================
   WooCommerce Buttons styles - end
   ========================================================================== */